

.drawer--right {
	@include transition(.5s all);
	//@include transition(.5s transform); 
	position:absolute;
	top:0;
	left:0;
	width:100%;
	width:100%;
	right:0;
	border:none;
	max-width: 100%;
	background:$c-store;
	color:$c-white;
	padding:0 $g-lr-margin;
	height:0;
	overflow-x:hidden;
	overflow-y:hidden;
	z-index:9999999;
	

	@include font-size($a-smaller-font-sizes); 
	
	@extend .noTransition;
	
	* {
		@extend .noTransition;
		@include font-size($p-smaller-font-sizes); 
		
		@include respond-to(mobile-portrait) {
			@include font-size($a-smaller-font-sizes); 
		}
		
		@include respond-to(mobile-landscape) {
			@include font-size($a-smaller-font-sizes); 
		}
	}
	
	.drawer input, .drawer textarea {
		border-color:$c-white;
	}
	
	form {
		position:absolute;
		width:100%;
		height:100vh;
		left:0;
		top:0;
		
		@include respond-to(mobile-portrait) {
			height:100%;
		}
		
		@include respond-to(mobile-landscape) {
			height:100% !important;
		}
	}
	
	
	.drawer__header{
		height:18.75vh;
		margin:0;
		border:none;
		min-height:120px;
		
		@include respond-to(mobile-portrait) {
			min-height:auto;
			height:10.75vh;
		}
		
		@include respond-to(mobile-landscape) {
			min-height:auto;
			height:16vw;
		}
		
		h2 {
			@extend %smaller-caps;
		}
		
		.drawer__close button {
			position:absolute;
			height:auto;
			right:0;
			top:0;
			padding:$g-tb-margin $g-lr-margin;
			
			.icon:before {
				font-size:1.2em;
			}
			
			@include respond-to(mobile-portrait) {
				display:none;
			}
	
			@include respond-to(mobile-landscape) {
				display:none;
			}
			
			&:hover, &:focus {
				color:$c-black;
				background:$c-store;
				
				span {
					color:$c-black;
					background:$c-store;
				}
			}
		}
	}
	
	#CartContainer {
		padding:0 $g-lr-margin;
		
		@include respond-to(mobile-portrait) {
			padding:$g-mobile-tb-margin $g-mobile-lr-margin;
		}
		
		@include respond-to(mobile-landscape) {
			padding:$g-mobile-tb-margin $g-mobile-lr-margin;
		}
	}
	
	.ajaxcart__inner {
		border:4px solid $c-white;
		padding:1.3vw 1.5vw;
		
		@include respond-to(mobile-portrait) {
			border:2px solid $c-white;
			padding:$g-mobile-tb-margin $g-mobile-lr-margin;
		}

		@include respond-to(mobile-landscape) {
			border:2px solid $c-white;
		}
		
		.ajaxcart__header {
			@include flexbox;
			margin-bottom:2.75vw;
			
			@include respond-to(mobile-portrait) {
				@include justify-content(space-between);
				display:none;
			}

			@include respond-to(mobile-landscape) {
				@include justify-content(space-between);
				display:none;
			}
			
			h2 {
				@extend %smaller-caps;
			}
		
		}
		
		.item-span {
			width:50%;
		}
		
		.price-span {
			width:12%;
			
			@include respond-to(mobile-portrait) {
				display:none;
				width:auto;
			}

			@include respond-to(mobile-landscape) {
				display:none;
				width:auto;
			}
		}
		
		.quantity-span {
			width:21%;
			
			@include respond-to(mobile-portrait) {
				width:auto;
				margin:3vw 0;
			}

			@include respond-to(mobile-landscape) {
				width:auto;
				margin:3vw 0;
			}
		}
		
		.total-span {
			width:12%;
			
			@include respond-to(mobile-portrait) {
				//display:none;
				width:auto;
			}

			@include respond-to(mobile-landscape) {
				//display:none;
				width:auto;
			}
		}
		
		.ajaxcart__product {
			margin-top:1.3vw;
		    padding: 0 0 1.3vw;
			border-bottom:4px solid $c-white;
			
			@include respond-to(mobile-portrait) {
				border-bottom:2px solid $c-white;
				margin-top:3vw;
			    padding: 0 0 3vw;
			}

			@include respond-to(mobile-landscape) {
				border-bottom:2px solid $c-white;
				margin-top:3vw;
			    padding: 0 0 3vw;
			}
			
			&:first-child {
				margin-top:0;
			}
			
			&:last-child {
				border-bottom:0;
			}
			
			&:hover {
				.ajaxcart__row {
					* {
						color:$c-black;
						border-color:$c-black;
					}
				
					.ajaxcart__qty, .js-qty__adjust--minus, .ajaxcart__qty--minus, .ajaxcart__qty--minus {
						color:$c-black;
						border-color:$c-black;
						
						* {
							color:$c-black;
							border-color:$c-black;
						}
					}
				}
			}
		}
		
		.ajaxcart__row {
			@include flexbox;
			
			border:none;
			margin:0;
			padding:0;
			
			@include respond-to(mobile-portrait) {
				@include justify-content(space-between);
				@include flex-wrap(wrap);
				@include align-items(center);
				//@include flex-direction(row-reverse);
			}

			@include respond-to(mobile-landscape) {
				@include justify-content(space-between);
				@include flex-wrap(wrap);
				@include align-items(center);
				//@include flex-direction(row-reverse);
			}
			
			.ajaxcart__qty--plus {
				border-color:$c-white;
			}
			
			.js-qty__adjust--plus, .ajaxcart__qty--plus {
				border-left:4px solid $c-white;
				
				@include respond-to(mobile-portrait) {
					border-left:2px solid $c-white;
				}

				@include respond-to(mobile-landscape) {
					border-left:2px solid $c-white;
				}
			}
			
			.js-qty input, .ajaxcart__qty input {
				border:4px solid $c-white;
				
				@include respond-to(mobile-portrait) {
					border:2px solid $c-white;
				}

				@include respond-to(mobile-landscape) {
					border:2px solid $c-white;
				}
			}
			
			.price-mobile {
				display:none !important;
				
				@include respond-to(mobile-portrait) {
					display:inline-block !important;
				}

				@include respond-to(mobile-landscape) {
					display:inline-block !important;
				}
			}
			
			.ajaxcart__product-title {
			    margin-top: -.15em;
				
				@include respond-to(mobile-portrait) {
					width:100%;
				}

				@include respond-to(mobile-landscape) {
					width:100%;
				}
				
				* {
					display:inline-block;
				}
			}
			
			.ajaxcart__qty {
				min-width:50%;
			    margin-top: -.18em;
			    line-height: 0;
				
				@include respond-to(mobile-portrait) {
					max-width:110px;
				}
				
				@include respond-to(mobile-landscape) {
					max-width:150px;
				}
				
				&:hover {
					button, input {
						* {
							color:$c-black;
						}
						color:$c-black;
						border-color:$c-black;
						
						&:hover {
							* {
								color:$c-black;
							}
							color:$c-black;
							border-color:$c-black;
						}
					}
				}
				
				
			}
			
			.ajaxcart__qty-adjust:focus, .ajaxcart__qty-adjust:hover {
				span { color:$c-black; }
			}
			
			.js-qty__adjust--minus, .ajaxcart__qty--minus {
				border-right:4px solid $c-white;
				
				@include respond-to(mobile-portrait) {
					border-right:2px solid $c-white;
				}

				@include respond-to(mobile-landscape) {
					border-right:2px solid $c-white;
				}
			}
			
			.ajaxcart__qty--minus {
				border:4px solid $c-white;
				
				@include respond-to(mobile-portrait) {
					border:2px solid $c-white;
				}

				@include respond-to(mobile-landscape) {
					border:2px solid $c-white;
				}
			}
			
			.ajaxcart__product-image {
				display:none;
				position:fixed;
				pointer-events:none;
				@include opacity(1);
				z-index:99999;
				-webkit-box-shadow: 0px 0px 11px 0px rgba(0,0,0,0.75);
				-moz-box-shadow: 0px 0px 11px 0px rgba(0,0,0,0.75);
				box-shadow: 0px 0px 11px 0px rgba(0,0,0,0.75);
				
				img {
					
				}
			}
		}
	}
	
	
	.ajaxcart__footer {
		@include flexbox;
		margin-bottom:15.75vh;
		
		@include respond-to(mobile-portrait) {
			@include justify-content(space-between);
			margin-bottom:0;
		}

		@include respond-to(mobile-landscape) {
			@include justify-content(space-between);
			margin-bottom:0;
		}
		
		.ajaxcart__subtotal {
			margin-left:50%;
			width:32%;
			
			@include respond-to(mobile-portrait) {
				margin-left:0%;
				width: 70%;
			}
		
			@include respond-to(mobile-landscape) {
				margin-left:0%;
				width: 70%;
			}
		}
		
		.ajaxcart__subtotal_price {
			
		}
		
		h3 {
			@extend %smaller-caps;
		}
		
		
	}
	
	.cart__checkout {
		position:absolute;
		right:$g-lr-margin;
		float:right;
		bottom:$g-tb-margin;
		width: 48vw;
		border:4px solid $c-white;
		padding:0;
		text-transform:uppercase;
		height:4vw;
		background:$c-store;
		padding: 8px 10px;
		
		span {
			@include font-size($a-smaller-font-sizes); 
		    padding-top: .2em;
		    display: block;
		    line-height: 1;
		}
		
		
		
		@include respond-to(mobile-portrait) {
			width:90vw;
			right:$g-mobile-lr-margin;
			bottom:$g-mobile-tb-margin;
			border:2px solid $c-white; 
			height:auto;
		}
		
		@include respond-to(mobile-landscape) {
			width:90vw;
			right:$g-mobile-lr-margin;
			bottom:$g-mobile-tb-margin;
			margin:$g-mobile-tb-margin 0;
			border:2px solid $c-white; 
			height:auto;
			position:fixed;
		}
		
		@include respond-to(mobile-portrait) {
			height:auto;
			bottom:$g-mobile-tb-margin;
		}
		
		&:hover, &:focus {
			border-color:$c-black;
			color:$c-black;
			
			span {
				color:$c-black;
			}
		}
		
		
	}
	
	button.btn--secondary {
		//background:transparent;
		//border:2px solid $c-white;
		color:$c-white;
	}
	
	* {
		color:$c-white;
	}
	
	a {
		color:$c-white;
		
		&:hover {
			color:$c-black;
		}
	}
}



.js-drawer-open-right .drawer--right {
	-ms-transform: translateX(0px);
	-webkit-transform: translateX(0px);
	transform: translateX(0px);
	height:100%;
}


.show-mobile-menu.js-drawer-open-right .drawer--right {
	-ms-transform: translateX(50vw);
	-webkit-transform: translateX(50vw);
	transform: translateX(50vw);
	height:100%;
}

.supports-fontface .icon:before {
    font-size: .85em;
    line-height: 1.4em;
}