.site-footer {
	background:$c-background;
	color:$c-white;
	display:none;
	width:100%;
	padding:0 $g-mobile-lr-margin;
	@include flex-direction(column);
	padding-bottom: 3vh;
	
	@include respond-to(mobile-portrait) {
		@include flexbox;
	}
	
	@include respond-to(mobile-landscape) {
		@include flexbox;
	}
	
	.social-icons {
		position:inherit;
		width:100%;
		@include flexbox;
		@include justify-content(space-between);
		
		li {
			text-transform:uppercase;
		}
	}
	
	.row {
		margin-top:$g-mobile-tb-margin;
		@include flexbox;
		@include justify-content(space-between);
		margin-bottom:0;
		
		* { 
			text-transform:uppercase;
		}
		
		li {
			@include flex(1);
			text-align:left;
			margin-bottom:0;
			
			&:last-child {
				text-align:right;
			}
			
			a {
				color:$c-white;
				@extend %smaller-caps-spacing;
			}
		}
		
		a {
			color:$c-white;
		}
		
		.copyright {
			font-size: .74em;
		}
		
		.button--contact {
			@include flexbox;
			@include justify-content(space-between);
			width:100%;
			span {
				@include flex(0);
			}
		}
		
		.gc-logo {
			//width: 53%;
			
			img {
				width: 66%;
				margin-top: .25em;
			}
		}
	}
}

.cart {
	.site-footer {
		display:none;
	}
}