.template-product, .product, .template-collection, .collection {
	.site-footer {
		background:$c-store;
	}
}


#productSelect {
	display:none !important;
}


.product-description-dummy {
	min-height:120px;
	width:100%;
	pointer-events:none;
	height:18.75vh;
	
	@include respond-to(mobile-portrait) {
		display:none;
	}

	@include respond-to(mobile-landscape) {
		display:none;
	}
}

.form-vertical {
	margin:0;
}

.single-product {
	float:left;
	position:relative;
	width:100%;
}

.product-single {
	margin:3vh 2vw;
	float:right;
	
	@include respond-to(mobile-portrait) {
		margin:12.5vh $g-mobile-lr-margin 0 $g-mobile-lr-margin ;
		width:90vw;
	}

	@include respond-to(mobile-landscape) {
		margin:29.5vh $g-mobile-lr-margin 0 $g-mobile-lr-margin;
	}
	
	.product-single__photos {
		@include respond-to(mobile-portrait) {
			margin-bottom:$g-mobile-tb-margin; 
		}

		@include respond-to(mobile-landscape) {
			margin-bottom:$g-mobile-tb-margin; 
		}
	}
	
	.product-title-mobile {
		display:none;
		margin-bottom:$g-mobile-tb-margin * 2;
		
		* {
			color:$c-white;
			text-transform:uppercase;
			@extend %smaller-caps-spacing;
		}
		
		@include respond-to(mobile-portrait) {
			display:block;
		}

		@include respond-to(mobile-landscape) {
			display:block;
		}
	}
	
	
	img:hover {
		cursor:s-resize;
	}
	
	
	
}


.product-description {
    width: 50vw;
	max-width:50vw;
    height: 100%;
    top: 0;
    position: fixed;
	@include opacity(0);
    left: 0;
	//overflow-y:scroll;
	@include flexbox;
	//@include justify-content(center);
	@include align-items(center);
	@include align-content(center);
	@include flex-direction(column);
	
	&.visible {
		@include opacity(1);
	}
	
	
	@include respond-to(mobile-portrait) {
		position:relative;
		width: 90vw;
		margin: 0 auto;
		margin-bottom:12.5vh;
		max-width:100vw;
	}

	@include respond-to(mobile-landscape) {
		position:relative;
		width: 90vw;
		margin: 0 auto;
		margin-bottom:29.5vh;
		max-width:100vw;
	}
	
	* { 
		@include font-size($p-smaller-font-sizes); 
		@include line-height($p-smaller-line-height); 
		vertical-align: top;
		
		@include respond-to(mobile-portrait) {
			@include font-size($p-font-sizes); 
			@include line-height($p-line-height); 
		}

		@include respond-to(mobile-landscape) {
			@include font-size($p-font-sizes); 
			@include line-height($p-line-height); 
		}
		
		color:$c-white;
		//line-height: 1.25;
	}
	
	.product-description-holder {
		//margin:0vh 6vw 0 2vw;
		    width: 92%;
		@include respond-to(mobile-portrait) {
			width:100%;
		}

		@include respond-to(mobile-landscape) {
			width:100%;
		}
		
		h1, .product-price {
			@include respond-to(mobile-portrait) {
				display:none;
			}

			@include respond-to(mobile-landscape) {
				display:none;
			}
		}
		
		#product-models {
		    display: block;
		    float: left;
		    width: 100%;
		}
		
		.add-to-cart-holder {
			z-index:999999;
			
			&.no-sticky {
				position:relative;
				padding:0;
			}
			
			@include respond-to(mobile-portrait) {
				@include flexbox;
				@include flex-direction(column);
				padding:$g-mobile-tb-margin $g-mobile-lr-margin;
				position:fixed;
				left:0;
				bottom:0;
				width:100%;
				background:$c-store;
				
				#product-variants {
					height:12vw;
				}
			}

			@include respond-to(mobile-landscape) {
				//@include flexbox;
				//@include flex-direction(column);
				//padding:$g-mobile-tb-margin $g-mobile-lr-margin;
				//position:fixed;
				left:0;
				bottom:0;
				width:100%;
				background:$c-store;
			}
		}
	}
	
	.selector-wrapper {
		//@include flexbox;
		//@include flex-direction(row);
	}
	
	//hide select drop down since it's controlled by radio buttons
	.selector-wrapper {
		display:none !important;
	}
	
	h1 {
		text-transform:uppercase;
		@extend %smaller-caps-spacing;
	}
	
	.product-description-text {
		margin-top:1em;
		
		table {
			margin: 2em 0 1em 0;
		}
		
		td {
			padding:0;
			border:none;
		}
		
		td:first-child {
			width:25% !important;
			
			@include respond-to(mobile-portrait) {
				width:30% !important;
			}

			@include respond-to(mobile-landscape) {
				width:30% !important;
			}
		}
	}
	
	#AddToCart {
		width:100%;
		//border:5px solid $c-white;
		
		@extend %borders;
		
		background:transparent;
		text-transform:uppercase;
		&:hover {
			border-color:$c-black;
			color:$c-black !important;
			
			span {
				color:$c-black;
			}
		}
	}
}

.size-product-landscape {
	
}

#product-models {
	span {
		width:25%;
		float:left;
		
		@include respond-to(mobile-portrait) {
			width:30%;
		}

		@include respond-to(mobile-landscape) {
			width:30%;
		}
	}
	
	span.separator {
		width:auto;
	}
	
	button, a {
		float:left;
		@extend %smaller-caps-spacing;
	}
	
	button.selected, a.selected {
		color:$c-black;
		pointer-events:none;
	}
	
	a:hover {
		color:$c-black;
	}
}


#product-variants {
	margin:0;
	clear:left;
	
	ul { 
		margin:0; 
		
		li {
			@include respond-to(mobile-portrait) {
				margin-left:-$g-mobile-lr-margin;
				margin-top:-$g-mobile-tb-margin;
			}

			@include respond-to(mobile-landscape) {
				//margin-left:-$g-mobile-lr-margin;
				//margin-top:-$g-mobile-tb-margin;
			}
			
			
			label {
				float:left;
			}
			
			.comma {
			    width: auto;
				float:left;
			    margin-right: .5em;
				
				@include respond-to(mobile-portrait) {
					display:none;
				}
			}
		}
	}
	
	span {
		width:25%;
		
		@include respond-to(mobile-portrait) {
			width:30%;
		}

		@include respond-to(mobile-landscape) {
			width:30%;
		}
	}
	
	li, span {
		float:left;
	}
	
	
	label {
		@include respond-to(mobile-portrait) {
			padding:$g-mobile-tb-margin $g-mobile-lr-margin;
		}

		@include respond-to(mobile-landscape) {
			//padding:$g-mobile-tb-margin $g-mobile-lr-margin;
		}
	}
	
	label.selected {
		color:$c-black;
	}
}