@font-face {
    font-family: 'f';
    src: url(f.eot);
    src: url(f-0.eot#iefix) format("embedded-opentype"), url(f.woff2) format("woff2"), url(f.woff) format("woff"), url(f.ttf) format("truetype"), url(f.svg#folio_bqregular) format("svg");
    font-weight: 700;
    font-style: normal
}

@font-face {
  font-family: "BeaufortW01-Regular";
  src: url("2c37f3699d9728b566e9bdc5b11e5180.eot"); /* IE9*/
  src: url("2c37f3699d9728b566e9bdc5b11e5180.eot?#iefix") format("embedded-opentype"), /* IE6-IE8 */
  url("2c37f3699d9728b566e9bdc5b11e5180.woff2") format("woff2"), /* chrome、firefox */
  url("2c37f3699d9728b566e9bdc5b11e5180.woff") format("woff"), /* chrome、firefox */
  url("2c37f3699d9728b566e9bdc5b11e5180.ttf") format("truetype"), /* chrome、firefox、opera、Safari, Android, iOS 4.2+*/
  url("2c37f3699d9728b566e9bdc5b11e5180.svg#Beaufort W01 Regular") format("svg"); /* iOS 4.1- */
}
%globalText {
	
	font-family:'f', Helvetica, Arial, sans-serif;
	
	@include font-size($p-font-sizes); 
	@include line-height($p-line-height); 
	color:$c-black;
}

body {
	@extend %globalText;
	-webkit-font-smoothing:antialiased;
	//text-shadow: 0px 0px 2px rgba(0, 0, 3, 0.6);
}

b, strong {
	font-weight:normal !important;
}

h1, h2, h3, h4, h5, h6, p, a, select, input, label, textarea, span, time {
	
	letter-spacing: initial !important;
	word-spacing:initial;
}

span, time, a {
	word-spacing:initial;
}

a {
	
	color:$c-white;
	text-decoration:none;
	outline:none;
	
	&:hover, &:focus {
		color:$c-black;
	}
	
	
	
} 

.site-footer a {
	color:#000;
}

a.disableAnimation {
	@extend .notransition;
}

.disable-link {
	pointer-events:none;
}

a, a:active, a:focus, input:focus {
	outline: none !important;
}

::-moz-selection { /* Code for Firefox */
    color: $c-background;
    background: $c-global;
}

::selection {
    color: $c-background;
    background: $c-black;
}

li {
	list-style:none;
}

address {
	font-style:normal; 
}

h1, h2, h3, h4, h5, h6, p, input, select {
	font-weight:normal;	
	color:$c-global;
}

h1, h2, h3, h4, h5, h6 {
	font-family: 'f';
}

 
p {

	margin:0 auto;
	
	@extend %globalText;
}

p {
	@include font-size($p-font-sizes); 
	@include line-height($p-line-height); 
	
	
}

h1 {
	@include font-size($p-font-sizes); 
	@include line-height($p-line-height); 
	
	
}

%h1 {
	@include font-size($p-font-sizes); 
	@include line-height($p-line-height); 
	
	
}

h2 {
	text-transform:uppercase;
	@include font-size($p-font-sizes); 
	@include line-height($p-line-height); 
	
	
}


.h1, .h2, .h3, .h4, .h5, .h6 {
	font-family:'f', Helvetica, Arial, sans-serif;
}

%smaller-caps-spacing {
	letter-spacing: .02em !important;
}

hr {
	border:none;
	border-top:1px solid $c-dark-gray;
}

li {
	line-height:1.5em;
	
}


.headerLabel {
	margin-top:30px;
}

