#body-products, .template-product {
	background:$c-store;
	
	.show-collection {
		display:none;
	}
}

#collection-template-container {
	@include respond-to(mobile-portrait) {
		position:relative;
		margin:0;
		height:auto;
	}

	@include respond-to(mobile-landscape) {
		position:relative;
		margin:0;
		height:auto;
	}
}

/*
*	HIDE THINGS
*/


#shop-screen {
	
	display:none;
	
	position:absolute;
	top:0;
	left:0;
	width:100%;
	height:100%;
	background-color:$c-store;
	z-index:99;
	
	
	@include justify-content(center);
	@include align-content(center);
	@include align-items(center);
	text-align:center;
	
	h1 {
		text-transform:uppercase;
	}
	
	&.shown {
		@include flexbox;
	}
	
	
	
}

.mobile-shop-title {
	display:none;
	
	@include respond-to(mobile-portrait) {
		display:block;
	}

	@include respond-to(mobile-landscape) {
		display:block;
	}
}

.product-container {
	@include flexbox;
	@include align-items(flex-start);
	@include flex-wrap(wrap);
	
	margin-top:3vh;
	
	@include respond-to(mobile-portrait) {
		margin-top:12.5vh;
	}

	@include respond-to(mobile-landscape) {
		margin-top:40vh;
	}
	
	h1 {
		color:$c-white;
		text-align:left;
		text-transform:uppercase;
		@extend %smaller-caps-spacing;
	    width: 100%;
	    margin: 0 $g-mobile-lr-margin;
		margin-bottom: $g-mobile-tb-margin * 2;
	}
}

.product-item {
	width:24vw;
	margin-left:3vw;
	margin-bottom:4vw;
	background-size:cover;
	
	
	@include respond-to(mobile-portrait) {
		width:100%;
		margin: 0 5vw 10.5vw 5vw;
	}

	@include respond-to(mobile-landscape) {
		width:100%;
		margin: 0 5vw 10.5vw 5vw;
	}
	
	&:hover {
		img {
			
		}
		
		a {
			color:$c-black;
			border-color:$c-black;
		}
	}
	 
	img {
		
		width:100%;
		display:none;
		@include opacity(0);
		
		&:first-child {
			display:block;
			
		}
		
		@include respond-to(mobile-portrait) {
			border:none;
		}

		@include respond-to(mobile-landscape) {
			border:none;
		}
	}
	
	.grid__image {
		background-size:cover;
		margin:0;
		margin-bottom:1.3vw;
		//border:3.5px solid $c-white;
		-webkit-box-shadow:inset 0px 0px 0px 3.5px $c-white;
		-moz-box-shadow:inset 0px 0px 0px 3.5px $c-white;
		box-shadow:inset 0px 0px 0px 3.5px $c-white;
			
		&:hover, &:focus {
			-webkit-box-shadow:inset 0px 0px 0px 3.5px $c-black;
			-moz-box-shadow:inset 0px 0px 0px 3.5px $c-black;
			box-shadow:inset 0px 0px 0px 3.5px $c-black;
		}
		
		@include respond-to(mobile-portrait) {
			margin-bottom:$g-mobile-tb-margin; 
		}

		@include respond-to(mobile-landscape) {
			margin-bottom:$g-mobile-tb-margin; 
		}
	}
	
	p, .h6, a {
		
		@include font-size($a-smaller-font-sizes); 
		@include line-height($p-smaller-line-height); 
		
		//font-size:1.675vw;
		text-transform:uppercase;
		color:$c-white;
		letter-spacing: .02em !important;
		display:block;
	}
	
	a:hover {
		color:$c-black;
	}
	
	.h6 {
		margin:0;
	}
}





.form-vertical input[type="radio"] {
	display:none;
}







.product-single__photos {
	float:right;
	margin:0;
	
	@include respond-to(mobile-portrait) {
		float:none;
	}

	@include respond-to(mobile-landscape) {
		float:none;
	}
	
	img {
		height:94vh;
		border:5px solid $c-black;
		margin-bottom:6.25vh;
		
		@include respond-to(mobile-portrait) {
			height:auto;
			border:none;
			width:100%;
		}

		@include respond-to(mobile-landscape) {
			height:auto;
			border:none;
		}
	}
	
	img:last-child {
		margin-bottom:0;
	}
}


.form-error, .errors { 
	border-color:#000;
	color:#000 !important;
	background-color:transparent;
	border-radius:0;
	border-size:5px;
	border:5px solid $c-black;
}


